.btn {
  display: inline-block;
  padding: 10px 20px;
  border: 1px solid transparent;
  background-color: transparent;
  border-radius: 4px;
  cursor: pointer;
  color: var(--background);
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  font-size: 20px;
  line-height: 1.5;
  transition: all 0.2s;
}
.btn-pink {
  border: 1px solid var(--btn-contrast);
  background-color: var(--btn-contrast);
}
.btn-pink:hover {
  background-color: var(--btn20);
  border: 1px solid var(--btn20);
  color: var(--title);
}
.btn-white {
  border: 1px solid var(--background);
  background-color: var(--background);
  color: var(--text);
}
.btn-white:hover {
  background-color: var(--text-copyright);
  border: 1px solid var(--paragraph);
}

.btn-area {
  position: absolute;
  background: transparent;
  border: transparent;
  margin: 0;
  padding: 0;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.carousel {
  display: block;
  margin: 0 auto;
  width: 100%;
  z-index: 4000;
}
.carousel__indicators {
  position: relative;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 15;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  list-style: none;
  padding: 0;
}
@media (max-width: 320px) {
  .carousel__indicators {
    column-gap: 20px;
    row-gap: 10px;
    justify-content: center;
  }
}
.carousel__item {
  box-sizing: content-box;
  flex: 0 0 auto;
  width: 30px;
  width: min(30px, max(var(--vw) * 30, 10px));
  height: min(30px, max(var(--vw) * 30, 10px));
  width: 26px;
  height: 26px;
  margin-right: 18px;
  margin-bottom: 15px;
  border-radius: 50%;
  cursor: pointer;
  background-color: var(--background);
  border: 3px solid var(--background);
  background-clip: border-box;
  will-change: transform;
  transition: opacity 0.6s ease;
}
.carousel__item:last-child {
  margin-right: 0;
}
@media (max-width: 420px) {
  .carousel__item {
    margin-right: 6px;
    margin-bottom: 4px;
  }
}
@media (max-width: 320px) {
  .carousel__item {
    margin-right: 0;
    margin-bottom: 0;
  }
}
.carousel__essentials {
  width: 16px;
  height: 16px;
}

