:root {
  --black:#000000;
  --attention: #F0EEDD;
  --text-attention: #D37657;
  --background: #ffffff;
  --background-text: #F5F1E9;
  --background-img: #FBF6EA;
  --background-footer:#6C674D;
  --indicator: #C87B5E;
  --title: #35312E;
  --text: #4F463F;
  --paragraph: #6E6259;
  --comment-img: #6E635A;
  --text-footer: #FCFAF6;
  --text-copyright: #bebba9;
  --btn: #D37657;
  --btn20: #e9825f;
  --dark-salmon: #E09D81;
  --light-blue-grey: #748EA1;
  --light-brown: #B76D47;
  --cement-grey: #7D836E;
  --persian-red: #C33636;
  --grey-silk: #CDC7B3;
  --green-grey: #595752;
  --forest-wolf: #D3C2CA;
  --oval: #f7f3eb;
  --footer-link: #F5F1E999;
  --footer-link-contrast: #DADAD2;
  --background-footer-contrast: #625D47;
  --text-copyright-contrast: #FFFFFF;
  --text-attention-contrast: #B24C2F;
  --btn-contrast: #C25433;
  --background-copyright-contrast: #79755D;
}

* {
  box-sizing: border-box;
}

*, ::after, ::before {
  box-sizing: border-box;
}

body {
  margin: 0;
}

:root {
  --index: calc(1vw + 1vh);
  --vw: calc(100vw/1440);
  --container-padding-x: min(165px, max(var(--vw)* 165, 24px));
  --container-width: min(1920px, max(var(--vw)* 1920, 24px));
}

a, a:link, a:visited {
  text-decoration: none;
}

* {
  font-family: "Roboto", sans-serif;
  font-optical-sizing: auto;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  color: var(--text);
}

.page {
  overflow-y: scroll;
  overflow-x: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}

.page.none-scroll {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 5px;
  width: 8px;
  height: 5px;
  background: transparent;
}

/* Optional: show position indicator in red */
/* border-radius: 10px; закругления плашки R не меньше ½ ширины*/
::-webkit-scrollbar-thumb {
  background: gainsboro;
  border-radius: 10px;
  border: 2px solid var(--background);
}

.reset {
  margin: 0;
  padding: 0;
}

.container {
  max-width: calc(var(--container-width) + var(--container-padding-x) * 2);
  margin: 0 auto;
  padding-left: var(--container-padding-x);
  padding-right: var(--container-padding-x);
  width: 100%;
}

.fadeOut {
  opacity: 0;
}

.fadeIn {
  opacity: 1;
}

.footer {
  margin-top: auto;
}

/* Прописная (заглавная)  первая буква  */
.text-capitalize {
  text-transform: capitalize;
}

/* Прописные (заглавные)  все  */
.text-uppercase {
  text-transform: uppercase;
}

/* Строчные (маленькие) все  */
.text-lowercase {
  text-transform: lowercase;
}

/* переносить или нет длинные слова */
.text-break-word {
  word-wrap: break-word;
  -webkit-text-size-adjust: 100%;
}

/* перенос слов на новую строку */
.text-break-all {
  word-break: break-all;
}

/* НЕ перенос слов на новую строку */
.text-nobreak {
  white-space: nowrap;
}

@media (max-width: 412px) {
  .anime {
    will-change: transform;
    animation-name: show-modular;
    animation-delay: 0s;
    animation-duration: 30s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
  .anime1 {
    animation-delay: 0s;
  }
  .anime2 {
    animation-delay: 5s;
  }
  .anime3 {
    animation-delay: 10s;
  }
  .anime4 {
    animation-delay: 15s;
  }
  .anime5 {
    animation-delay: 20s;
  }
  .anime6 {
    animation-delay: 25s;
  }
}
@keyframes show-modular {
  0% {
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  15% {
    opacity: 1;
  }
  17% {
    opacity: 0;
  }
  21% {
    opacity: 0;
  }
  99% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
