.icon-bar {
  --h: 2px;
  --top: 11px;
  background: var(--text);
  position: relative;
  top: 12px;
  display: block;
  width: 31.5px;
  height: var(--h);
}
.icon-bar::before {
  top: calc(var(--top) * -1);
  will-change: transform;
  transition: top 0.25s 0.25s, transform 0.25s;
}
.icon-bar::after {
  top: var(--top);
  will-change: transform;
  transition: top 0.25s 0.25s, transform 0.25s;
}
.icon-bar::after, .icon-bar::before {
  background: var(--text);
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--h);
}

.underline-anime {
  padding-bottom: 2px;
  position: relative;
}

.underline-anime::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--text);
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
  transition: opacity 300ms, transform 900ms;
  will-change: transform;
}

.underline-anime:hover::after {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

@media (max-width: 420px) {
  .fadeIn--open .menu {
    animation: 0.5s fadeInLeft forwards;
  }
  .fadeIn--open .icon-bar::before {
    top: 0;
    transform: rotate(-45deg);
    will-change: transform;
    transition: top 0.25s, transform 0.25s 0.25s;
  }
  .fadeIn--open .icon-bar::after {
    top: 0;
    transform: rotate(45deg);
    will-change: transform;
    transition: top 0.25s, transform 0.25s 0.25s;
  }
  .fadeIn--open .icon-bar {
    background: 0 0;
  }
}
@keyframes fadeOutLeft {
  from {
    opacity: 1;
    transform: none;
  }
  to {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
}
@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
